<template>
  <div class="pannelPadding-10">
    <div class="pannelPaddingBg-10 table_grid table_title">
      <div>
        <h5 class="mg-12">待办事项基本信息</h5>
        <table>
          <tr>
            <th>待处理事项</th>
            <td colspan="3">{{ dataInfo.dclsx }}</td>
          </tr>
          <tr>
            <th>计划完成时间</th>
            <td>{{ dataInfo.jhwcsj }}</td>
            <th>实际完成时间</th>
            <td>{{ dataInfo.sjwcsj }}</td>
          </tr>
          <tr>
            <th>责任方</th>
            <td colspan="3">{{dataInfo.fwznsm}}</td>
          </tr>
          <tr>
            <th>公司责任人</th>
            <td colspan="3">{{dataInfo.fwznsm}}</td>
          </tr>
          <tr>
            <th>学校责任人</th>
            <td colspan="3">{{dataInfo.fwznsm}}</td>
          </tr>
          <tr>
            <th>第三方责任人</th>
            <td colspan="3">{{dataInfo.fwznsm}}</td>
          </tr>
          <tr>
            <th>关注人</th>
            <td colspan="3">{{dataInfo.fwznsm}}</td>
          </tr>
        </table>
      </div>
      <div>
        <h5 class="mg-12">待办事项反馈</h5>
        <div>
          <el-button class="mg-12" size="mini" type="success" @click="fkShow = true">添加反馈</el-button>
          <el-table :data="fkList" border style="width: 100%">
            <el-table-column prop="fksj" label="反馈时间"></el-table-column>
            <el-table-column prop="fkr" label="反馈人" ></el-table-column>
            <el-table-column prop="fksm" label="反馈说明"></el-table-column>
            <el-table-column prop="wczt" label="完成状态"></el-table-column>
            <el-table-column prop="jhwcsj" label="计划完成时间"></el-table-column>
          </el-table>
        </div>
      </div>
      <div text-right>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="records"
        ></el-pagination>
      </div>
    </div>

     <fkDialog :show.sync="fkShow" @handleCommitFk="handleCommitFk"></fkDialog>   
  </div>
</template>

<script>
import fkDialog from "@/views/BusinessPage/meetOnline/fk-dialog.vue";
export default {
  data() {
    return {
      fkShow:false,
      dataInfo: {},
      currentPage: 1,
      pageSize: 10,
      records: 0,
      fkList: []
    };
  },
  mounted() {
 
  },
  components: {fkDialog},

  methods: {
    //   反馈成功
    handleCommitFk(){

    },
    //  分页
    handleCurrentChange(data) {
      this.currentPage = data;
      this.pageServiceItemPlan();
    },
    // 获取待处理事项
    getService() {
      this.$get(this.API.getServiceItem, {
        wid: this.$route.query.id
      }).then(res => {
        if (res.state == "success") {
          this.dataInfo = res.data;
        } else {
        }
      });
    },
    // 反馈列表
    pageServiceItemPlan() {
      this.$get(this.API.pageServiceItemPlan, {
        curPage: this.currentPage,
        pageSize: this.pageSize,
        zbwid: this.$route.query.id,
        onlyNewest: 1
      }).then(res => {
        if (res.state == "success") {
          if (!!res.data.rows) {
            this.fkList = res.data.rows;
          } else {
            this.fkList = [];
          }
          this.records = res.data.records;
        } else {
        }
      });
    }
  }
};
</script>

<style>
</style>



